import React, { useEffect } from "react";
import { helpshift, drift } from "./helpers.js";

const NoLoader: React.FC<{}> = () => {
  useEffect(() => {
    if (window.helpshiftConfig) {
      helpshift.hide();
    }
    if (window.drift) {
      drift.hide();
    }
  });
  return null;
};

export default NoLoader;
