// This is largely lifted from gatsby-plugin-google-analytics but changed to use segment
import React from "react";

const OutboundLink = (props) => {
  const { onClick, target, noEvent, href, children, className, role } = props;
  const domProperties = { onClick, target, href, className, role };

  return (
    <a
      {...domProperties}
      onClick={(e) => {
        if (typeof onClick === "function") {
          onClick();
        }

        let redirect = true;
        if (
          e.button !== 0 ||
          e.altKey ||
          e.ctrlKey ||
          e.metaKey ||
          e.shiftKey ||
          e.defaultPrevented
        ) {
          redirect = false;
        }
        if (target && target.toLowerCase() !== "_self") {
          redirect = false;
        }

        if (window.analytics && !noEvent) {
          window.analytics.track("Click: Outbound Link", { url: href });
        } else if (redirect) {
          document.location = href;
        }

        return false;
      }}
    >
      {children}
    </a>
  );
};

export default OutboundLink;
