import { compose, withStateHandlers, withHandlers, lifecycle } from "recompose";
import React from "react";
import Helmet from "react-helmet";
import qs from "qs";
import Cookies from "js-cookie";
import uuid from "uuid";
import * as ramda from "ramda";
import classnames from "classnames";

import colors from "styles/colors";
import { marketingAttributionProperties } from "utils/analytics";
import Footer from "components/footer/index";
import Header from "components/reusable/organisms/Header";
import PromotionToast from "components/toast/PromotionToast";
import ProductToast from "components/toast/ProductToast";
import previewImage from "assets/img/brex-preview.png";
import "styles/main.scss";
import { UrlParametersContext } from "contexts/UrlParameters";
import { promotionToast, productToast } from "components/toast/messages";
import Banner from "./banner";

import WideContainer from "../WideContainer";
import { Content, HeaderWrapper } from "./elements";
import { layoutReducer, LayoutState } from "./layoutModel";
import {
  componentDidMount,
  acceptCookies,
  rejectCookies,
} from "./layoutWorkflows";
import { useModel } from "hooks";

import ChatBotLoader from "components/ChatBotLoader";

const defaultRefCodes = {
  aws: "aws_launch_campaign",
  capterra: "capterra",
  hubspot: "hubspot_initial_outreach",
  clerky: "clerky",
  pilot: "pilot",
  orrick: "orrick",
  xero: "xero_lp",
  newzealand: "newzealand",
  yc: "yc_offer",
  payoneer: "payoneer",
};

const defaultUrlParams = {
  ref_code: defaultRefCodes,
};

const generateCanonicalURL = (location) => {
  let pathname = location.pathname || "/";

  if (!pathname.endsWith("/")) {
    pathname += "/";
  }

  const myUrl = `https://brex.com${pathname}`;

  return [
    {
      rel: "canonical",
      key: myUrl,
      href: myUrl,
    },
  ];
};

const getMetaTags = (
  description,
  ogTitle,
  ogDescription,
  ogImage,
  indexPage,
) => [
  {
    name: "description",
    content: description,
  },
  {
    name: "robots",
    content: indexPage ? "index,follow" : "noindex,follow",
  },
  {
    property: "og:image",
    content: ogImage,
  },
  {
    property: "og:title",
    content: ogTitle,
  },
  {
    property: "og:description",
    content: ogDescription,
  },
  {
    property: "og:type",
    content: "website",
  },
  {
    name: "twitter:card",
    content: "summary_large_image",
  },
  {
    name: "twitter:site",
    content: "@brexhq",
  },
  {
    name: "twitter:title",
    content: ogTitle,
  },
  {
    name: "twitter:description",
    content: ogDescription,
  },
  {
    name: "twitter:image",
    content: ogImage.startsWith("/static/")
      ? `https://www.brex.com${ogImage}`
      : `https:${ogImage}`,
  },
];

const renderChildren = ({
  children,
  defaultContentBackground,
  defaultGutterBackground,
  extraProps, // used to pass headerOffset to children
}) => (
  <React.Fragment>
    {React.Children.toArray(children).map((child, index) => (
      <WideContainer
        // eslint-disable-next-line react/no-array-index-key
        key={index}
        gutterBackground={
          child.props.gutterBackground || defaultGutterBackground
        }
        contentBackground={
          child.props.contentBackground || defaultContentBackground
        }
      >
        {React.cloneElement(child, extraProps)}
      </WideContainer>
    ))}
  </React.Fragment>
);

/**
 * General purpose layout. Use this to control everything about how content is presentented including meta tags, page title, indexability, etc
 * @param {object} props Component Props
 * @param {string} props.title The page's <title> content
 * @param {string?} props.ogTitle OpenGraph title
 * @param {string?} props.ogDescription OpenGraph description
 * @param {string?} props.ogImage OpenGraph preview image
 * @param {boolean?} props.indexPage Whether or not search engines should index the page
 */
const Layout = ({
  title = "Brex | The Smartest Corporate Credit Card",
  description = "Credit card with no personal guarantee, instant approvals, higher limits, and modern payment terms. Powered by the Mastercard network with best-in-class fraud protection.",
  children,
  whiteContentBackground = false,
  whiteGutterBackground = false,
  location,
  urlParameters,
  ogTitle,
  ogImage = previewImage,
  ogDescription,
  hideDefaultHeader,
  hideDefaultFooter,
  companyType,
  indexPage = true,
  script,
  headerOffset,
  calculateHeaderOffset,
  setHeaderRef,
  showPromotionToast,
  hidePromotionToast,
  showProductToast,
  hideProductToast,
  isHeaderCutoff,
  customHeaderButton,
}) => {
  const [state, dispatch] = useModel(layoutReducer, new LayoutState());
  const { showCCPABanner } = state;

  React.useEffect(() => dispatch(componentDidMount), []); //eslint-disable-line
  React.useEffect(() => {
    if (!window.Helpshift) return;
    if (showCCPABanner) window.Helpshift && window.Helpshift("hide");
    else window.Helpshift && window.Helpshift("show");
  }, [showCCPABanner]);

  return (
    <main>
      <Helmet
        title={title}
        meta={getMetaTags(
          description,
          ogTitle || title,
          ogDescription || description,
          ogImage,
          indexPage,
        )}
        link={generateCanonicalURL(location)}
      >
        {script}
      </Helmet>
      <UrlParametersContext.Provider value={urlParameters}>
        <div
          className={classnames({
            "margin-wrapper": true,
            "margin-wrapper__inverted": whiteGutterBackground,
          })}
        >
          {!hideDefaultHeader && (
            <HeaderWrapper
              ref={setHeaderRef}
              isHeaderCutoff={isHeaderCutoff}
              headerOffset={headerOffset}
            >
              {promotionToast && showPromotionToast && (
                <PromotionToast
                  message={promotionToast.message}
                  key={promotionToast.name}
                  hideToast={hidePromotionToast}
                />
              )}
              <WideContainer gutterBackground={colors.black}>
                <Header
                  siteTitle="Brex"
                  location={location}
                  companyType={companyType}
                  headerOffset={headerOffset}
                  customHeaderButton={customHeaderButton}
                />
              </WideContainer>
              {showProductToast && (
                <ProductToast
                  message={productToast.message}
                  key={productToast.name}
                  hideToast={hideProductToast}
                />
              )}
            </HeaderWrapper>
          )}
          <Content marginTop={headerOffset}>
            {children &&
              renderChildren({
                children,
                defaultContentBackground: whiteContentBackground
                  ? colors.white
                  : colors.black,
                defaultGutterBackground: whiteGutterBackground
                  ? colors.white
                  : colors.black,
                extraProps: { headerOffset },
              })}
            <Banner
              showCCPABanner={showCCPABanner}
              acceptCookies={() => dispatch(acceptCookies)}
              rejectCookies={() => dispatch(rejectCookies)}
            />
          </Content>
          {!hideDefaultFooter && <Footer />}
        </div>
      </UrlParametersContext.Provider>
      <ChatBotLoader />
    </main>
  );
};

const initializeAnalytics = () => {
  // Generate a uuid for identifying anonymous users and store
  // it in a cookie, refreshing the expiry date if it already exists.
  // This allows us to make use of this ID before Segment is fully loaded,
  // so we can initialize other libraries that need to use on the
  // anonymous ID in parallel instead of blocking on segment's initialization.
  // Also, for users with certain ad-blockers and tracking blocking extensions
  // Segment's library may never actually load, so we can't always depend on
  // this functionality from Segment's library.
  const anonymousIdStorageKey = "brexAnonymousId";

  // We use a cookie for the anonymous ID so we can share it seamlessly between
  // dashboard.brex.com and brex.com
  // This requires actually computing the correct root domain to set for the cookie
  const cookieRootDomain = ramda.cond([
    [ramda.startsWith("localhost"), ramda.always("localhost")],
    [ramda.endsWith("stg.brex.com"), ramda.always(".stg.brex.com")],
    [ramda.endsWith("rc.brex.com"), ramda.always(".rc.brex.com")],
    [ramda.endsWith("brex.com"), ramda.always(".brex.com")],
  ]);

  const getAnonymousId = () => Cookies.get(anonymousIdStorageKey);

  const initializeAnonymousId = () => {
    const existingAnonymousId = getAnonymousId();
    const anonymousId = existingAnonymousId || uuid.v4();
    // refresh the cookie regardless if it already exists
    Cookies.set(anonymousIdStorageKey, anonymousId, {
      expires: 365 * 10,
      domain: cookieRootDomain(window.location.host),
    });
    return anonymousId;
  };

  const anonymousId = initializeAnonymousId();

  // window.analytics doesn't get initialized in development
  if (window.analytics) {
    window.analytics.ready(() => {
      window.analytics.user().anonymousId(anonymousId);
    });
  }
};

const _lifecycle = {
  // TODO: we should move refcode and gclid to use cookies as well to share
  // with dashboard instead of appending url parameters to links manually.
  initializeUrlParameter(parameter, defaultValueMap = null) {
    const sessionStorageValue = sessionStorage.getItem(parameter);

    const urlPathValue = qs.parse(this.props.location.search, {
      ignoreQueryPrefix: true,
    })[parameter];

    let parameterValue = sessionStorageValue || urlPathValue;

    if (!!defaultValueMap && !parameterValue) {
      parameterValue =
        defaultValueMap[this.props.location.pathname.replace(/\//g, "")];
    }

    if (!parameterValue) {
      return;
    }

    sessionStorage.setItem(parameter, parameterValue);
    this.setState((state) =>
      state
        ? {
            urlParameters: {
              ...state.urlParameters,
              [parameter]: parameterValue,
            },
          }
        : { urlParameters: { [parameter]: parameterValue } },
    );
  },
  async componentDidMount() {
    initializeAnalytics();
    if (this.props.refCode) {
      sessionStorage.setItem("ref_code", this.props.refCode);
    }
    marketingAttributionProperties.map((property) =>
      this.initializeUrlParameter(property, defaultUrlParams[property]),
    );
    // autoScrollBehavior makes scrolling be immediate rather than smooth
    if (this.props.autoScrollBehavior) {
      document.documentElement.className = "auto-scroll-behavior";
    } else {
      document.documentElement.className = "";
    }

    // when window resizes, should redo calculation
    window.addEventListener("resize", this.props.calculateHeaderOffset);

    window.addEventListener("scroll", this.props.calculateHeaderTop);

    const hash = window.location.hash;
    if (hash) {
      const element = document.getElementById(hash.substr(1));
      if (element) {
        // need to wait for images to load, because componentDidMount doesn't wait
        // step 1: get images in document from document.images
        const documentImages = document.images;
        const imageLoadedPromises = [];

        // step 2: create promise for every image
        // document.images is not an array so can't use .map()
        for (
          let imageIndex = 0;
          imageIndex < documentImages.length;
          imageIndex += 1
        ) {
          const image = documentImages[imageIndex];
          if (!image.complete) {
            imageLoadedPromises.push(
              new Promise((resolve) => {
                image.addEventListener("load", () => resolve());
                // at 5 seconds, just resolve
                window.setTimeout(() => resolve(), 5000);
              }),
            );
          }
        }

        // step 3: once all images have loaded, scroll to location
        // set offset for content
        Promise.all(imageLoadedPromises).then(() => {
          // if hideDefaultHeader is true, the actual header might be different
          // but we have no way of knowing what the header is

          window.scrollTo(0, element.offsetTop);
        });
      }
    }

    if (localStorage[promotionToast.name]) {
      this.props.hidePromotionToast();
    }
    if (!localStorage[productToast.name]) {
      this.props.showProductToastOnPage();
    }

    // do initial calculation for height of header
    this.props.calculateHeaderOffset();
  },
  componentDidUpdate(oldProps) {
    const showPromotionToastOld = oldProps.showPromotionToast;
    const showPromotionToastNew = this.props.showPromotionToast;

    const showProductToastOld = oldProps.showProductToast;
    const showProductToastNew = this.props.showProductToast;

    if (showPromotionToastOld !== showPromotionToastNew) {
      this.props.calculateHeaderOffset();
    }

    if (showProductToastOld !== showProductToastNew) {
      this.props.calculateHeaderOffset();
    }
  },
  componentWillUnmount() {
    window.removeEventListener("resize", this.props.calculateHeaderOffset);

    window.removeEventListener("scroll", this.props.calculateHeaderTop);
  },
};

export default compose(
  withStateHandlers(
    ({ hideDefaultHeader }) => ({
      headerOffset: hideDefaultHeader ? 0 : 80,
      showPromotionToast: false,
      showProductToast: false,
      isHeaderCutoff: false,
      showCCPABanner: false,
    }),
    {
      setHeaderOffset: () => (headerOffset) => ({
        headerOffset,
      }),
      hidePromotionToast: () => () => {
        localStorage[promotionToast.name] = true;
        return {
          showPromotionToast: false,
        };
      },
      hideProductToast: () => () => {
        localStorage[productToast.name] = true;
        return {
          showProductToast: false,
        };
      },
      showProductToastOnPage: () => () => ({
        showProductToast: true,
      }),
      calculateHeaderTop: ({ isHeaderCutoff }, { headerCutoff }) => () => {
        // headerCutoff sets a scrollY such that below
        // that point, the header scrolls away.

        if (!headerCutoff) {
          return;
        }

        if (window.scrollY >= headerCutoff) {
          if (!isHeaderCutoff) {
            return {
              isHeaderCutoff: true,
            };
          }
        } else if (isHeaderCutoff) {
          return {
            isHeaderCutoff: false,
          };
        }
      },
    },
  ),
  withHandlers(() => {
    // get ref and calculate header offset

    let headerRef;
    let headerOffset = 80; // Default for the offchance the ref can't be read
    return {
      setHeaderRef: () => (ref) => {
        headerRef = ref;
      },
      calculateHeaderOffset: ({ setHeaderOffset, hideDefaultHeader }) => () => {
        if (headerRef) {
          headerOffset = headerRef.getBoundingClientRect().height;
        }
        if (hideDefaultHeader) {
          // non-default headers have 0 offset because they are not sticky, and thus accounted for
          // in the DOM.
          headerOffset = 0;
        }
        // set offset for content
        setHeaderOffset(headerOffset);
      },
    };
  }),
  lifecycle(_lifecycle),
)(Layout);
