export class LayoutState {
  showCCPABanner = false;
}

export enum LayoutActionType {
  ShowBanner,
  HideBanner,
}

export class ShowBannerAction {
  type = LayoutActionType.ShowBanner;
}

export class HideBannerAction {
  type = LayoutActionType.HideBanner;
}

export type LayoutAction = ShowBannerAction | HideBannerAction;

export const layoutReducer: React.Reducer<LayoutState, LayoutAction> = (
  state: LayoutState,
  action: LayoutAction,
): LayoutState => {
  switch (action.type) {
    case LayoutActionType.HideBanner:
      return { ...state, showCCPABanner: false };
    case LayoutActionType.ShowBanner:
      return { ...state, showCCPABanner: true };
    default:
      return state;
  }
};
