import React, { useEffect, useState } from "react";

import InlineScriptLoader from "utils/addScript";
import HelpShiftIcon from "./HelpShiftIcon";

import { helpshift, drift } from "./helpers.js";

const HelpShiftLoader: React.FC<{ initOnMount: boolean }> = ({
  initOnMount,
}) => {
  const [shouldLoadHSScript, setShouldLoadHSScript] = useState(initOnMount);
  const [userIconInteraction, setUserIconInteraction] = useState(false);

  useEffect(() => {
    if (window.helpshiftConfig) {
      helpshift.updatePageUrl();
      helpshift.show();
    }
    if (window.drift) {
      drift.hide();
    }
  });

  const launchHS = () => {
    // We always want this open because of the fake icon
    setUserIconInteraction(true);
    window.Helpshift("open");
  };

  const showFakeIcon = () => {
    if (userIconInteraction) return false;
    if (window.helpshiftConfig?.platformId) return false;
    return true;
  };
  return (
    <>
      {showFakeIcon() && ( // if we haven't already loaded HS - show a fake icon
        <HelpShiftIcon onClick={() => setShouldLoadHSScript(true)} />
      )}
      {shouldLoadHSScript && (
        <InlineScriptLoader
          scriptText={`
(function () {
                  var PLATFORM_ID = "brex_platform_20191104214658560-60c283890053a52",
                  DOMAIN = "brex",
                  LANGUAGE = "en";

                  window.helpshiftConfig = {
                    platformId: PLATFORM_ID,
                    domain: DOMAIN,
                    language: LANGUAGE
                  };
                })();

                !function(t,e){if("function"!=typeof window.Helpshift){var n=function(){n.q.push(arguments)};n.q=[],window.Helpshift=n;var i,a=t.getElementsByTagName("script")[0];if(t.getElementById(e))return;i=t.createElement("script"),i.async=!0,i.id=e,i.src="https://webchat.helpshift.com/webChat.js";var o=function(){window.Helpshift("init")};window.attachEvent?i.attachEvent("onload",o):i.addEventListener("load",o,!1),a.parentNode.insertBefore(i,a)}else window.Helpshift("update")}(document,"hs-chat");`}
          windowObject="helpshiftConfig"
          scriptId="helpshift"
          callback={() => launchHS()}
        />
      )}
    </>
  );
};

export default HelpShiftLoader;
