import { DateTime } from "luxon";
import moment from "moment";

export const formatDateTime = (value) =>
  DateTime.fromISO(value).toFormat("LLL dd yyyy");

export const formatDate = (date) => {
  const dateObject = moment(date, "YYYY-MM-DD");
  return dateObject.format("MM/D/YYYY");
};

export const formatUrl = (url) => url.replace(/^(javascript:)*/i, "");

export const formatDollars = (dollarAmount) =>
  `$${dollarAmount.toLocaleString()}`;

export const formatKebabCase = (url) => url.toLowerCase().replace(" ", "-");

export const getBrowserName = () => {
  if (typeof navigator !== "undefined") {
    const isMobile = /iPhone|iPad|iPod/i.test(navigator.userAgent);
    if (
      navigator.userAgent.indexOf("Edge") > -1 &&
      navigator.appVersion.indexOf("Edge") > -1
    ) {
      return "Edge";
    } else if (
      navigator.userAgent.indexOf("Opera") !== -1 ||
      navigator.userAgent.indexOf("OPR") !== -1
    ) {
      return "Opera";
    } else if (navigator.userAgent.indexOf("Chrome") !== -1) {
      return "Chrome";
    } else if (navigator.userAgent.indexOf("Safari") !== -1 || isMobile) {
      return "Safari";
    } else if (navigator.userAgent.indexOf("Firefox") !== -1) {
      return "Firefox";
    } else if (
      navigator.userAgent.indexOf("MSIE") !== -1 ||
      !!document.documentMode === true
    ) {
      // IF IE > 10
      return "IE";
    } else {
      return "unknown";
    }
  }

  return "Chrome";
};

export const chooseImageFormat = (png, webP) => {
  const currentBrowser = getBrowserName();

  if (
    currentBrowser === "IE" ||
    currentBrowser === "Safari" ||
    currentBrowser === "unknown"
  ) {
    return png;
  }

  return webP;
};

export const getCurrentMonthAndLastDay = () => {
  const today = new Date();
  const month = today.toLocaleString("default", { month: "long" });
  const lastDay = new Date(
    today.getFullYear(),
    today.getMonth() + 1,
    0,
  ).getDate();

  return { fullDate: ` ${month} ${lastDay}`, month: month.toLocaleLowerCase() };
};
