import React from "react";
import styled from "@emotion/styled";

// design uses a multiples of 8 based scale for spacing
const baseSpacingUnit = 8;
// design uses a 12 column scale, with 56px padding and 16px gutters
const maxColumns = 12;
const defaultPadding = baseSpacingUnit * 7;
const defaultGutterWidth = baseSpacingUnit * 2;
const defaultWrappedSpacing = baseSpacingUnit * 6;

interface ColumnProps {
  span: number;
  spanMedium?: number;
  spanSmall?: number;
}

interface RowInnerProps {
  wrappedSpacing?: number;
  horizontalAlignment?: string;
  verticalAlignment?: string;
  reversedFlexDirection?: boolean;
  reversedFlexWrap?: boolean;
  gutterWidth?: number;
}

interface RowContainerProps {
  padding?: number;
  withTopMargin?: boolean;
}

type RowProps = RowInnerProps & RowContainerProps;

export const breakPoints = {
  smallLaptop: "@media(max-width: 1100px)",
  mediumLaptop: "@media(max-width: 1200px)",
  medium: "@media(max-width: 991px)",
  small: "@media(max-width: 576px)",
};

const columnWidthStyles = (span: number) => ({
  flexBasis: `${(span * 100) / maxColumns}%`,
  maxWidth: `${(span * 100) / maxColumns}%`,
});

const hideAtZeroSpan = (span: number) => ({
  display: span === 0 ? "none" : "block",
});

export const Column = styled.div<ColumnProps>(({ span = 1, ...props }) => {
  const spanMedium = props.spanMedium === undefined ? span : props.spanMedium;
  const spanSmall = // eslint-disable-line operator-linebreak
    props.spanSmall === undefined ? spanMedium : props.spanSmall;

  return {
    flexGrow: 1,
    flexShrink: 1,
    ...hideAtZeroSpan(span),
    ...columnWidthStyles(span),
    [breakPoints.medium]: {
      ...hideAtZeroSpan(spanMedium),
      ...columnWidthStyles(spanMedium),
    },
    [breakPoints.small]: {
      ...hideAtZeroSpan(spanSmall),
      ...columnWidthStyles(spanSmall),
    },
  };
});

export const rowPaddingStyles = (padding: number) => ({
  paddingLeft: padding,
  paddingRight: padding,
});

export const rowGutterStyles = (gutterWidth: number) => ({
  marginLeft: (-1 * gutterWidth) / 2,
  marginRight: (-1 * gutterWidth) / 2,
});

export const columnGutterStyles = (gutterWidth: number) => ({
  paddingLeft: gutterWidth / 2,
  paddingRight: gutterWidth / 2,
});

export const rowWrappedSpacingStyles = (
  wrappedSpacing = defaultWrappedSpacing,
) => ({
  marginTop: -1 * wrappedSpacing,
});

export const columnWrappedSpacingStyles = (
  wrappedSpacing = defaultWrappedSpacing,
) => ({
  marginTop: wrappedSpacing,
});

const RowInner = styled.div<RowInnerProps>(
  ({
    wrappedSpacing,
    horizontalAlignment = "center",
    verticalAlignment = "flex-start",
    reversedFlexDirection = false,
    reversedFlexWrap = false,
    gutterWidth = defaultGutterWidth,
  }) => ({
    display: "flex",
    justifyContent: horizontalAlignment,

    // wrap upwards instead of downwards
    flexWrap: reversedFlexWrap ? "wrap-reverse" : "wrap",

    // flex leftwards instead of rightwards
    flexDirection: reversedFlexDirection ? "row-reverse" : "row",

    alignItems: verticalAlignment,

    // combine negative + positive spacing of parent and children
    // so spacing only appears when wrapped
    // https://stackoverflow.com/a/30891910
    // For more info on how this works, refer to Bootstrap's gutter implementation:
    // https://getbootstrap.com/docs/4.1/layout/grid/#how-it-works
    ...rowGutterStyles(gutterWidth),
    ...rowWrappedSpacingStyles(wrappedSpacing),
    [`>${Column}`]: {
      ...columnGutterStyles(gutterWidth),
      ...columnWrappedSpacingStyles(wrappedSpacing),
    },
  }),
);

const RowContainer = styled.div<RowContainerProps>(
  ({ padding = defaultPadding, withTopMargin = false }) => ({
    marginTop: withTopMargin ? 48 : 0,
    ...rowPaddingStyles(padding),
    [breakPoints.medium]: rowPaddingStyles((padding * 6) / baseSpacingUnit),
    [breakPoints.small]: rowPaddingStyles((padding * 4) / baseSpacingUnit),
  }),
);

export const Row: React.SFC<RowProps> = ({
  padding,
  withTopMargin,
  ...props
}) => (
  <RowContainer padding={padding} withTopMargin={withTopMargin}>
    <RowInner {...props} />
  </RowContainer>
);
