import React from "react";

interface SpacerProps {
  height?: React.CSSProperties["height"];
  width?: React.CSSProperties["width"];
  minWidth?: React.CSSProperties["minWidth"];
}

export default function Spacer({ height, width, minWidth }: SpacerProps) {
  return (
    <div
      className="brex-spacer"
      style={{ height, width, minWidth, flexBasis: width || height }}
    ></div>
  );
}
