import React, { useEffect, useContext } from "react";
import ChatBotContext from "contexts/ChatBot";

import DriftLoader from "./drift";
import HelpShiftLoader from "./helpshift";
import NoLoader from "./NoLoader";

import { matchesPath } from "./helpers";
import { DRIFT_PATHS, LANDING_PATHS } from "./constants";

const ChatBotSwitcher: React.FC<{}> = () => {
  const { botPath, setBotPath, HSInitOnMount, swappingDriftBot } = useContext(
    ChatBotContext,
  );

  useEffect(() => {
    if (matchesPath(DRIFT_PATHS) && !swappingDriftBot) {
      setBotPath("drift");
    } else if (matchesPath(LANDING_PATHS)) {
      setBotPath("none");
    } else {
      setBotPath("helpshift");
    }
  }, [setBotPath, swappingDriftBot]);

  if (botPath === "drift") {
    return <DriftLoader />;
  }
  if (botPath === "helpshift") {
    return <HelpShiftLoader initOnMount={HSInitOnMount} />;
  }
  return <NoLoader />;
};

export default ChatBotSwitcher;
