const qs = require("qs");

function parseArgs(queryString) {
  return qs.parse(queryString, {
    ignoreQueryPrefix: true,
  });
}

/**
 * @param {string | void} env
 * @param {string} hostName
 * @param {string} queryString
 * @returns {boolean}
 * Allows forcing ccpa banner open on staging and dev environments
 */
module.exports = function shouldForceCCPABannerOpen(queryString) {
  if (!queryString) return false;

  const routeParams = parseArgs(queryString);
  // const isStaging = env === "staging";
  // const isDev = env === "development";
  // const isStagingOrDev = isStaging || isDev;
  const shouldShowCCPABanner =
    routeParams && routeParams.showCCPABanner === "true";

  return shouldShowCCPABanner;
};
