import { useState, useEffect } from "react";
import { getBrowserName } from "utils/formatting";

const useImageFormat = () => {
  const [useWebP, setBrowserName] = useState(true);
  useEffect(() => {
    const userAgentBrowser = getBrowserName();
    if (
      userAgentBrowser === "IE" ||
      userAgentBrowser === "Safari" ||
      userAgentBrowser === "unknown"
    ) {
      setBrowserName(false);
    }
  }, [useWebP]);

  return useWebP;
};

export default useImageFormat;
