export default class Padding {
  static none() {
    return new Padding(0, 0, 0, 0);
  }
  public static all(amt: number) {
    return new Padding(amt, amt, amt, amt);
  }

  public static symmetric(args: { horizontal?: number; vertical?: number }) {
    const { horizontal, vertical } = args;
    return new Padding(vertical, horizontal, vertical, horizontal);
  }

  public static only(only: {
    top?: number;
    right?: number;
    bottom?: number;
    left?: number;
  }) {
    return new Padding(only.top, only.right, only.bottom, only.left);
  }

  // hack to emulate nominal typing
  //@ts-ignore
  private readonly tag = "PADDING";

  private constructor(
    public paddingTop?: number,
    public paddingRight?: number,
    public paddingBottom?: number,
    public paddingLeft?: number,
  ) {}

  public toCss() {
    return `${this.paddingTop} ${this.paddingRight} ${this.paddingBottom} ${this.paddingLeft}`;
  }
}
