/**
 * If third party script isn't already loaded load and execute it
 * Allows cleanup incase the script is only used on that one route
 *
 * @param  {string} scriptText - script the 3rd party has asked you to load (if inside script tags)
 * @param  {string} scriptSrc - 3rd parth script if src attr
 * @param  {string} scriptId - DOM id.
 * @param  {string} windowObject - The object the api pushes to the window
 * @param  {function} callback - Action to complete after script has loaded.
 * @param  {function} removeOnUnmount - Remove this inline script on unmount
 *
 * @return {void} No return
 */
import React, { useEffect } from "react";

const insertScript = (
  scriptSrc: string | undefined,
  scriptText: string | undefined,
  id: string,
): void => {
  const script = document.createElement("script");
  if (scriptSrc) {
    script.src = scriptSrc;
  }
  if (scriptText) {
    script.textContent = scriptText.toString();
  }

  script.type = "text/javascript";
  script.setAttribute("id", id);
  script.async = true;
  document.body.appendChild(script);
};

const InlineScriptLoader: React.FC<{
  scriptText?: string;
  scriptSrc?: string;
  scriptId: string;
  windowObject?: string;
  removeOnUnmount?: boolean;
  callback?: Function;
}> = ({
  scriptText,
  windowObject = "noObject",
  scriptId,
  scriptSrc = undefined,
  removeOnUnmount = false,
  callback,
}) => {
  const id = `script-${scriptId}`;
  useEffect(() => {
    //. Check the script hasn't already been initied before
    if (typeof window !== "undefined" && !window[windowObject]) {
      insertScript(scriptSrc, scriptText, id);
      if (callback) {
        callback();
      }
    }

    return () => {
      // Cleanup function
      if (removeOnUnmount) {
        document.getElementById(id)?.remove(); // If element exists remove it
      }
    };
  });

  return <></>;
};

export default InlineScriptLoader;
