import React from "react";
import { Link as GatsbyLink } from "gatsby";

// This internal link ensures that all to routes have a trailing slash for
// consistency. We do ensure, however, that no trailing slash is appended to
// paths that have url params or anchor links -- given that the trailing slash
// after those two elements might break their functionality.
const Link = ({ children, to, ...rest }) => {
  let href = to;
  if (!to.endsWith("/") && !to.includes("#") && !to.includes("?")) {
    href += "/";
  }

  return (
    <GatsbyLink to={href} {...rest}>
      {children}
    </GatsbyLink>
  );
};

export default Link;
