import {
  LayoutAction,
  ShowBannerAction,
  HideBannerAction,
  LayoutState,
} from "./layoutModel";
import { AsyncWorkflow } from "hooks/useModelHook";
import shouldForceCCPABannerOpen from "./shouldForceCCPABannerOpen";
import Cookies from "js-cookie";
import { rejectCookiesItems } from "utils/analytics";

type LayoutWorkflow = AsyncWorkflow<LayoutAction, LayoutState>;

export const componentDidMount: LayoutWorkflow = (dispatch) => {
  if (!localStorage.ccpa) {
    let domain =
      window.location.hostname !== "localhost" ? ".brex.com" : "localhost";
    if (window.location.href.indexOf("netlify") > -1) {
      domain = window.location.hostname;
    }

    const trackingPreferences = JSON.parse(
      Cookies.get("tracking-preferences", {
        domain,
      }),
    );

    if (
      trackingPreferences !== undefined &&
      Number(trackingPreferences.version) !== 1
    ) {
      dispatch(new ShowBannerAction());
    } else {
      // Segment track call to tell GTM that it can drop CCPA-affected cookies
      window.analytics.track("ccpa-approved");
      // In case further regulation becomes relevent in the future,
      // track a generic "Cookie Compliance Checked" event.
      // If applicable, send "<LegislationName>.accepted/rejected",
      // otherwise, "noBannerRequired"
      window.analytics.track("Cookie Compliance Checked", {
        target: "noBannerRequired",
      });
    }
  }

  const result = shouldForceCCPABannerOpen(window.location.search);

  if (result) dispatch(new ShowBannerAction());
};

export const rejectCookies: LayoutWorkflow = (dispatch) => {
  localStorage.ccpa = "rejected";
  window.analytics.track("Cookie Compliance Checked", {
    target: "CCPA.rejected",
  });

  dispatch(new HideBannerAction());
};

export const acceptCookies: LayoutWorkflow = (dispatch) => {
  localStorage.ccpa = "accepted";
  Object.keys(rejectCookiesItems).forEach(
    /* eslint-disable no-return-assign */
    (cookie) => (rejectCookiesItems[cookie] = true),
  );
  const value = {
    version: 1,
    destinations: rejectCookiesItems,
  };

  let domain =
    window.location.hostname !== "localhost" ? ".brex.com" : "localhost";
  if (window.location.href.indexOf("netlify") > -1) {
    domain = window.location.hostname;
  }

  Cookies.set("tracking-preferences", value, {
    expires: 365,
    domain,
  });
  Cookies.set("mutiny.user.token", localStorage.getItem("mutiny.user.token"), {
    path: "/",
    domain,
  });

  window.analytics.track("Cookie Compliance Checked", {
    target: "CCPA.accepted",
  });
  window.analytics.track("ccpa-approved");

  dispatch(new HideBannerAction());
};
