import React from "react";
import { gothamFonts } from "styles/fonts";

export type TextVariant = keyof typeof gothamFonts;

interface TextProps {
  variant: TextVariant;
  style?: React.CSSProperties;
  children: React.ReactNode;
}

export default function Text({
  variant = "body-m",
  style = {},
  children,
}: TextProps) {
  return (
    <div
      className="brex-text"
      style={{
        flex: "0 0 auto",
        ...(gothamFonts[variant] as React.CSSProperties),
        ...style,
      }}
    >
      {children}
    </div>
  );
}
