import React from "react";
import styled from "@emotion/styled";
import { margin } from "polished";

const WideContainerOuter = styled("div")(({ background }) => ({
  width: "100%",
  background,
}));

const WideContainerInner = styled("div")(({ background }) => ({
  maxWidth: 1680,
  background,
  ...margin(0, "auto"),
}));

const WideContainer = ({ contentBackground, gutterBackground, children }) => (
  <WideContainerOuter background={gutterBackground}>
    <WideContainerInner background={contentBackground}>
      {children}
    </WideContainerInner>
  </WideContainerOuter>
);

export default WideContainer;
