import React, { useState } from "react";
import ChatBotContext from "contexts/ChatBot";
import ChatBotSwitcher from "./Switcher";
import { helpshift } from "./helpers";

const ChatBotLoader: React.FC<{}> = () => {
  const [botPath, setBotPathState] = useState("none");
  const [swappingDriftBot, setSwappingDriftBot] = useState(false);
  const [HSInitOnMount, setHSInitOnMount] = useState(false);

  const setBotPath = (botPath: string) => {
    setBotPathState(botPath);
  };

  // If we have already loaded HS open it, if not init it

  const initHS = () => {
    setBotPathState("helpshift");
    setSwappingDriftBot(true);
    if (window.Helpshift) {
      return helpshift.open();
    } else {
      return setHSInitOnMount(true);
    }
  };

  return (
    <ChatBotContext.Provider
      value={{ botPath, setBotPath, initHS, HSInitOnMount, swappingDriftBot }}
    >
      <ChatBotSwitcher />
    </ChatBotContext.Provider>
  );
};

export default ChatBotLoader;
