import { lifecycle, withHandlers } from "recompose";

export const withClickAnalytics = (type, target) =>
  withHandlers({
    onClick: ({ onClick }) => (event) => {
      if (onClick) {
        onClick(event);
      }

      if (window.analytics) {
        window.analytics.track(`Click: ${type}`, {
          target,
        });
      }
    },
  });

export const withImpressionAnalytics = (name) =>
  lifecycle({
    componentDidMount: () => {
      if (window.analytics) {
        window.analytics.track(`Impression: ${name}`);
      }
    },
  });
