import React from "react";
import { compose, lifecycle } from "recompose";
import cookies from "js-cookie";

import { Wrapper, AcceptButton, RejectButton } from "./elements";
import Text from "components/reusable/atoms/text";
import VStack from "components/reusable/atoms/layout/vstack";
import colors from "styles/colors";
import Spacer from "components/reusable/atoms/layout/Spacer";
import HStack from "components/reusable/atoms/layout/hstack";
import { Row, Column } from "components/Grid";
import { CrossAxisAlignment } from "components/reusable/atoms/layout/AxisAlignment";
import InternalLink from "components/reusable/atoms/links/InternalLink";
import Padding from "components/reusable/atoms/layout/Padding";

interface BooleanProps {
  showCCPABanner: boolean;
}

interface BannerProps extends BooleanProps {
  rejectCookies: (event: React.MouseEvent) => void;
  acceptCookies: (event: React.MouseEvent) => void;
}

interface CycleProps extends BooleanProps {
  acceptCookies: Function;
}

const Banner = ({
  rejectCookies,
  acceptCookies,
  showCCPABanner,
}: BannerProps) => {
  return (
    <Wrapper showCCPABanner={showCCPABanner}>
      <Row gutterWidth={0} padding={0}>
        <Column span={7}>
          <VStack
            horizontalAlignment={CrossAxisAlignment.FILL}
            padding={Padding.symmetric({ vertical: 12 })}
          >
            <Text variant="body-s" style={{ color: colors.spaceGray }}>
              Brex uses cookies and tracking technologies to improve your
              experience on our site. By clicking Reject you will opt-out of
              target technologies that share data with advertising partners. For
              more information see the{" "}
              <InternalLink to="/privacy">Brex Privacy Policy</InternalLink>.
            </Text>
            <Spacer height={24} />
            <HStack spacing={10}>
              <AcceptButton onClick={acceptCookies}>Accept</AcceptButton>
              <RejectButton onClick={rejectCookies}>Reject</RejectButton>
            </HStack>
          </VStack>
        </Column>
      </Row>
    </Wrapper>
  );
};

export default compose<BannerProps, {}>(
  lifecycle<CycleProps, {}, {}>({
    componentDidMount() {
      if (!localStorage.ccpa) {
        cookies.remove("mutiny.user.token", {
          path: "/",
          domain: ".brex.com",
        });
      }
    },
  }),
)(Banner);
