import React, { useEffect } from "react";

import qs from "qs";
import { Link } from "gatsby";
import { UrlParametersContext } from "contexts/UrlParameters";
/* eslint-disable camelcase */

interface LinkObjectProps {
  ref_code?: string;
  companyType?: string;
}

type LoadPage = {
  (rawPath: string): Promise<Object>;
};
type GatsbyPublicLoader = {
  loadPage: LoadPage;
};

// TODO: Define a window type globally with all
// the things we actually use on it
type WindowWithGatsby = typeof window & {
  ___loader: GatsbyPublicLoader;
};

const SignUpReferenceLink: React.FC<{
  className?: string;
  companyType?: string;
  refCode?: string;
}> = ({ children, className, companyType, refCode }) => {
  useEffect(() => {
    // Force Gatsby to preload the signup chunk
    // as soon as this button mounts.
    // This is equivalent to the mechanism Gatsby
    // uses internally when the user hovers on a
    // gatsby-link element.
    (window as WindowWithGatsby).___loader.loadPage("/signup");
  }, []);
  return (
    <UrlParametersContext.Consumer>
      {(urlParameters: {}) => {
        let linkObject: LinkObjectProps = {
          ...urlParameters,
          companyType,
        };
        if (refCode !== undefined) {
          linkObject = { ...linkObject, ref_code: refCode };
        }
        return (
          <Link
            to={`/signup${qs.stringify(linkObject, { addQueryPrefix: true })}`}
            className={className}
          >
            {children}
          </Link>
        );
      }}
    </UrlParametersContext.Consumer>
  );
};

export default SignUpReferenceLink;
