import React, { FunctionComponent } from "react";
import { ToastWrapper, CloseIcon, CloseText } from "./elements";
import { ToastProps } from "./types";

const Toast: FunctionComponent<ToastProps> = ({
  onClose,
  children,
  backgroundColor,
  height,
  closeColor,
}) => {
  return (
    <ToastWrapper
      backgroundColor={backgroundColor}
      height={height}
      className="display-none-in-percy"
    >
      {children}
      {onClose && (
        <CloseIcon onClick={onClose}>
          <CloseText closeColor={closeColor}>x</CloseText>
        </CloseIcon>
      )}
    </ToastWrapper>
  );
};

export default Toast;
