/*
  Use this file for any small scripts to be injected into Gatsby
  hooks at build time. Anything not injected at build time should
  go in the src/utils folder.
*/

// Below is a quick utility wrapper for Helpshift,
// which simplifies usage and provides a custom way to
// hide/show the help box based on path. Doing it via a style
// element prevents having to wait for Helpshift's script to
// initialize, avoiding pop-in on initial load.
export const helpshift = {
  show() {
    const hideRuleEl = document.getElementById("hide-chat");
    if (hideRuleEl) {
      hideRuleEl.remove();
    }
  },
  hide() {
    // Do nothing if the element already exists
    if (document.getElementById("hide-chat")) {
      return;
    }
    const hideRuleEl = document.createElement("style");
    hideRuleEl.type = "text/css";
    hideRuleEl.id = "hide-chat";
    // Helpshift inserts two adjacent iframes; the first
    // is the actual chatbox, the second is the icon. The icon
    // is given no useful identifier, so using its properties here.
    hideRuleEl.innerText = `
      body > iframe#hs-web-sdk-iframe, body > iframe[style*="position: fixed; bottom: 28px; right: 28px;"] {
         display: none !important;
      }
    `;
    document.head.appendChild(hideRuleEl);
  },
  updatePageUrl() {
    window.Helpshift("setCustomIssueFields", {
      page_url: {
        type: "string",
        value: window.location.pathname,
      },
    });
  },
  open() {
    window.Helpshift("open");
  },
};

export const drift = {
  hide() {
    const driftWidget = document.getElementById("drift-widget-container");
    if (driftWidget) {
      driftWidget.style.display = "none";
    }
  },
  show() {
    const driftWidget = document.getElementById("drift-widget-container");
    if (driftWidget) {
      driftWidget.style.display = "block";
    }
  },
};

/**
 * Checks list of paths for a wildcard match against the
 * current path, after applying proprietary fixes to the
 * present path to work across environments
 *
 * @param  {Array.<string|object>} paths - asterisk wildcard or regex paths
 * @return {boolean} Whether the current location matches anything in paths
 */
export const matchesPath = (paths) => {
  const currentPath = window.location.pathname
    // Strip any branch pre-paths before finding match
    // Should remove this after Netlify merge
    .replace(/\/_branch_\/[^/]*\//, "/")
    // Strip trailing slash before the check
    .replace(/\/$/, "");

  return paths.some((wildcardOrRegExp) =>
    (wildcardOrRegExp instanceof RegExp
      ? wildcardOrRegExp
      : RegExp("^" + wildcardOrRegExp.replace(/\*/g, ".*") + "$")
    ).test(currentPath),
  );
};
