import React from "react";
import { disableBodyScroll, clearAllBodyScrollLocks } from "body-scroll-lock";

import logo from "assets/img/logos/brex/logo-white.svg";
import menuIcon from "assets/img/icons/white/menu.svg";
import xIcon from "assets/img/icons/white/x.svg";
import arrowIcon from "assets/img/icons/orange/arrow-up.svg";
import { withClickAnalytics } from "hocs/analytics";

import InternalLink from "components/reusable/atoms/links/InternalLink";
import MenuLink from "components/reusable/molecules/MenuLink";

import withClickOutside from "react-onclickoutside";

import {
  productsMenuContent,
  solutionsMenuContent,
  rewardsMenuContent,
  resourcesMenuContent,
  contactInfoList,
} from "./messages";

import {
  Arrow,
  ButtonGroup,
  ProductsMenuContainer,
  Cards,
  ProductsTitle,
  Cash,
  Dot,
  Primary,
  ContactInfoList,
  ContactInfoNumber,
  ContactInfoTitle,
  DesktopApplyButtonStyled,
  DesktopContainer,
  DesktopLeft,
  DesktopLogoContainer,
  DesktopPopupMenu,
  DesktopRight,
  DesktopSignInLinkStyled,
  HiddenMenu,
  Logo,
  MenuSubtitle,
  MenuTitle,
  MenuTitleContainer,
  MobileApplyButtonStyled,
  MobileBackButton,
  MobileBackButtonTitle,
  MobileCategories,
  MobileCategory,
  MobileCategoryTitle,
  MobileContactInfoContainer,
  MobileContainer,
  MobileIcon,
  MobileLogoContainer,
  MobilePopupMenu,
  MobileSignInButtonStyled,
  Nav,
  NavInner,
  NavLink,
  NavLinkList,
  ResourcesMenuColumn,
  ResourcesMenuContainer,
  ResourcesMenuLinkList,
  ResourcesMenuSpacer,
  RewardsMenuContainer,
  StandardMenuContainer,
  StandardMenuLinkList,
} from "./elements";

const DesktopSignInLink = withClickAnalytics(
  "Link",
  "header.signin",
)(DesktopSignInLinkStyled);
const DesktopApplyButton = withClickAnalytics(
  "Button",
  "header.signup",
)(DesktopApplyButtonStyled);

const MobileApplyButton = withClickAnalytics(
  "Button",
  "header.signup",
)(MobileApplyButtonStyled);
const MobileSignInButton = withClickAnalytics(
  "Button",
  "header.signin",
)(MobileSignInButtonStyled);

const isActive = (location, path) => location.pathname.startsWith(path);

const menuLinkListItems = ({
  links,
  location,
  noBorders = false,
  noBorderLastOnly = false,
  noArrow = false,
  hideSubtitle = false,
}) =>
  links.map(({ url, title, subtitle, analyticsPath }, index) => {
    const Link = withClickAnalytics("Link", analyticsPath)(MenuLink);

    return (
      <Link
        to={url}
        title={title}
        subtitle={hideSubtitle ? undefined : subtitle}
        analyticsPath={analyticsPath}
        isActive={isActive(location, url)}
        noBorderBelowTitle={
          noBorders || (noBorderLastOnly && index === links.length - 1)
        }
        noArrow={noArrow}
        key={url}
      />
    );
  });

const productsDesktopMenu = (location) => (
  <ProductsMenuContainer key="products-menu-container">
    <Cards>
      <ProductsTitle>{productsMenuContent.cards.title}</ProductsTitle>
      {menuLinkListItems({
        links: productsMenuContent.cards.links,
        location,
      })}
    </Cards>
    <Cash>
      <ProductsTitle>
        {productsMenuContent.cash.title}
        <Dot>{"\u2022"}</Dot>
        <Primary>New</Primary>
      </ProductsTitle>
      {menuLinkListItems({
        links: productsMenuContent.cash.links,
        location,
      })}
    </Cash>
  </ProductsMenuContainer>
);

const standardMenuContainer = ({ location, content }) => (
  <StandardMenuContainer key="standard-menu-container">
    {content.map(({ title, titleNewFlag, subtitle, links }) => (
      <React.Fragment key={title}>
        <MenuTitleContainer>
          <MenuTitle>
            {title}
            {titleNewFlag && (
              <React.Fragment>
                <Dot>{"\u2022"}</Dot>
                <Primary>New</Primary>
              </React.Fragment>
            )}
          </MenuTitle>
          <MenuSubtitle>{subtitle}</MenuSubtitle>
        </MenuTitleContainer>
        <StandardMenuLinkList>
          {menuLinkListItems({
            links,
            location,
            noBorderLastOnly: true,
            hideSubtitle: true,
          })}
        </StandardMenuLinkList>
      </React.Fragment>
    ))}
  </StandardMenuContainer>
);

const productsMobileMenu = (location) =>
  standardMenuContainer({
    location,
    content: [productsMenuContent.cards, productsMenuContent.cash],
  });

const solutionsMenu = (location) =>
  standardMenuContainer({
    location,
    content: solutionsMenuContent,
  });

const rewardsMenu = (location) => (
  <RewardsMenuContainer key="rewards-menu-container">
    {menuLinkListItems({ links: rewardsMenuContent.links, location })}
  </RewardsMenuContainer>
);

const resourcesMenu = (location) => (
  <ResourcesMenuContainer key="resources-menu-container">
    <ResourcesMenuColumn>
      {menuLinkListItems({
        links: resourcesMenuContent.generalResources.links,
        location,
        noArrow: true,
        noBorders: true,
      })}
    </ResourcesMenuColumn>
    <ResourcesMenuColumn>
      <MenuTitleContainer>
        <MenuTitle>{resourcesMenuContent.learningResources.title}</MenuTitle>
        <MenuSubtitle>
          {resourcesMenuContent.learningResources.subtitle}
        </MenuSubtitle>
      </MenuTitleContainer>
      <ResourcesMenuSpacer />
      <ResourcesMenuLinkList>
        {menuLinkListItems({
          links: resourcesMenuContent.learningResources.links,
          location,
          noBorderLastOnly: true,
        })}
      </ResourcesMenuLinkList>
    </ResourcesMenuColumn>
    <ResourcesMenuColumn>
      <MenuTitleContainer>
        <MenuTitle>{resourcesMenuContent.partnerResources.title}</MenuTitle>
        <MenuSubtitle>
          {resourcesMenuContent.partnerResources.subtitle}
        </MenuSubtitle>
      </MenuTitleContainer>
      <ResourcesMenuSpacer />
      <ResourcesMenuLinkList>
        {menuLinkListItems({
          links: resourcesMenuContent.partnerResources.links,
          location,
          noBorderLastOnly: true,
        })}
      </ResourcesMenuLinkList>
    </ResourcesMenuColumn>
  </ResourcesMenuContainer>
);

const navLinks = [
  {
    key: "products",
    title: "Products",
    menu: {
      default: productsDesktopMenu,
      mobile: productsMobileMenu,
    },
  },
  {
    key: "solutions",
    title: "Solutions",
    menu: {
      default: solutionsMenu,
    },
  },
  {
    key: "rewards",
    title: "Rewards",
    menu: {
      default: rewardsMenu,
    },
  },
  {
    key: "resources",
    title: "Resources",
    menu: {
      default: resourcesMenu,
    },
  },
];

const renderBrexLogo = () => (
  <InternalLink to="/">
    <Logo alt="Brex" src={logo} />
  </InternalLink>
);

const renderContactInfoList = () => (
  <ContactInfoList>
    {contactInfoList.map(({ title, number }) => (
      <React.Fragment key={title}>
        <ContactInfoTitle>{title}</ContactInfoTitle>
        <ContactInfoNumber href={number.tel}>{number.string}</ContactInfoNumber>
      </React.Fragment>
    ))}
  </ContactInfoList>
);

class Header extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isTop: true,
      showDesktopPopupMenu: props.showFromStoryBook || false,
      activeDesktopIndex: props.activeIndex || null,
      showMobilePopupMenu: props.showFromStoryBook || false,
      activeMobileIndex: props.activeIndex || null,
    };

    this.handleClickOutside = this.handleClickOutside.bind(this);

    this.desktopHandlers = navLinks.map((_, index) => ({
      onMouseEnter: () => this.handleDesktopMouseEnter({ index }),
      onMouseLeave: () => this.handleDesktopMouseLeave(),
    }));

    this.toggleMobileMenu = this.toggleMobileMenu.bind(this);

    this.mobileHandlers = navLinks.map((_, index) => ({
      onClick: () => this.handleMobileCategoryClick({ index }),
    }));

    this.handleMobileBack = this.handleMobileBack.bind(this);

    this.mobilePopupMenuRef = React.createRef();
  }

  // using global event listener because Gatsby is still on React v15
  // when its upgraded we can explore onScroll handler on the nav element
  componentDidMount() {
    window.addEventListener("resize", this.handleResize);
    window.addEventListener("scroll", this.handleScroll);
    this.currentWindowWidth = window.innerWidth;
  }

  componentDidUpdate(_, prevState) {
    // if mobile popup menu is shown, disable body scrolling.
    // if it's hidden, enable body scrolling.

    const oldShowMobilePopupMenu = prevState.showMobilePopupMenu;
    const { showMobilePopupMenu } = this.state;

    if (!oldShowMobilePopupMenu && showMobilePopupMenu) {
      disableBodyScroll(this.mobilePopupMenuRef.current);
    } else if (oldShowMobilePopupMenu && !showMobilePopupMenu) {
      clearAllBodyScrollLocks();
    }
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.handleResize);
    window.removeEventListener("scroll", this.handleScroll);
    clearAllBodyScrollLocks();
  }

  handleClickOutside = () => {
    clearAllBodyScrollLocks();
    this.setState({
      showDesktopPopupMenu: false,
      activeDesktopIndex: null,
      showMobilePopupMenu: false,
      activeMobileIndex: null,
    });
  };

  handleDesktopMouseEnter = ({ index }) => {
    this.setState({
      showDesktopPopupMenu: true,
      activeDesktopIndex: index,
    });
  };

  handleDesktopMouseLeave = () => {
    this.setState({
      showDesktopPopupMenu: false,
      activeDesktopIndex: null,
    });
  };

  handleResize = () => {
    // if resizing from mobile to desktop, hide the menu and enable
    // body scroll

    const newWindowWidth = window.innerWidth;
    if (this.currentWindowWidth < 992 && newWindowWidth >= 992) {
      clearAllBodyScrollLocks();
      this.setState({
        showMobilePopupMenu: false,
        activeMobileIndex: null,
      });
    }

    this.currentWindowWidth = newWindowWidth;
  };

  handleScroll = () => {
    const { isTop } = this.state;
    const { headerOffset } = this.props;
    if (isTop && window.scrollY > headerOffset) {
      this.setState({ isTop: false });
    } else if (!isTop && window.scrollY < headerOffset) {
      this.setState({ isTop: true });
    }
  };

  toggleMobileMenu = () => {
    const { showMobilePopupMenu } = this.state;

    this.setState({
      showMobilePopupMenu: !showMobilePopupMenu,
    });
  };

  handleMobileCategoryClick = ({ index }) => {
    this.setState({
      activeMobileIndex: index,
    });
  };

  handleMobileBack = () => {
    this.setState({
      activeMobileIndex: null,
    });
  };

  renderDesktop = () => {
    const { isTop, activeDesktopIndex } = this.state;
    const { customHeaderButton } = this.props;
    return (
      <DesktopContainer>
        <DesktopLeft>
          <DesktopLogoContainer>{renderBrexLogo()}</DesktopLogoContainer>
          <NavLinkList>
            {navLinks.map(({ key, title }, index) => (
              <NavLink
                key={key}
                active={index === activeDesktopIndex}
                onMouseEnter={this.desktopHandlers[index].onMouseEnter}
                onMouseLeave={this.desktopHandlers[index].onMouseLeave}
              >
                {title}
              </NavLink>
            ))}
          </NavLinkList>
        </DesktopLeft>
        <DesktopRight>
          {renderContactInfoList()}
          <DesktopSignInLink href="https://dashboard.brex.com">
            Sign In
          </DesktopSignInLink>
          {customHeaderButton ? (
            customHeaderButton()
          ) : (
            <DesktopApplyButton isTop={isTop}>
              See if you qualify
            </DesktopApplyButton>
          )}
        </DesktopRight>
      </DesktopContainer>
    );
  };

  renderDesktopPopupMenu() {
    const { location } = this.props;
    const { activeDesktopIndex, showDesktopPopupMenu } = this.state;
    const navLink = navLinks[activeDesktopIndex];

    if (!navLink || !showDesktopPopupMenu) {
      return (
        <HiddenMenu>
          {navLinks.map(({ menu }) => menu.default(location))}
        </HiddenMenu>
      );
    }

    const { menu } = navLink;
    const hiddenLinks = navLinks.filter(
      (_, index) => index !== activeDesktopIndex,
    );

    return (
      <React.Fragment>
        <DesktopPopupMenu
          onMouseEnter={this.desktopHandlers[activeDesktopIndex].onMouseEnter}
          onMouseLeave={this.desktopHandlers[activeDesktopIndex].onMouseLeave}
        >
          {menu.default(location)}
        </DesktopPopupMenu>
        <HiddenMenu>
          {hiddenLinks.map(({ menu }) => menu.default(location))}
        </HiddenMenu>
      </React.Fragment>
    );
  }

  renderMobile = () => {
    const { showMobilePopupMenu } = this.state;

    return (
      <MobileContainer>
        <MobileLogoContainer>{renderBrexLogo()}</MobileLogoContainer>
        <MobileIcon
          src={showMobilePopupMenu ? xIcon : menuIcon}
          onClick={this.toggleMobileMenu}
        />
      </MobileContainer>
    );
  };

  renderMobilePopupMenu = () => {
    const { location, headerOffset, customHeaderButton } = this.props;
    const { activeMobileIndex } = this.state;
    const height = window.innerHeight - headerOffset;

    if (activeMobileIndex === null) {
      return (
        <MobilePopupMenu ref={this.mobilePopupMenuRef} height={height}>
          <ButtonGroup>
            {customHeaderButton ? (
              customHeaderButton()
            ) : (
              <MobileApplyButton>See if you qualify</MobileApplyButton>
            )}
            <MobileSignInButton href="https://dashboard.brex.com">
              Sign In
            </MobileSignInButton>
          </ButtonGroup>
          <MobileCategories>
            {navLinks.map(({ key, title }, index) => (
              <MobileCategory
                key={key}
                noBorder={index === navLinks.length - 1}
                onClick={this.mobileHandlers[index].onClick}
              >
                <MobileCategoryTitle>{title}</MobileCategoryTitle>
                <Arrow src={arrowIcon} />
              </MobileCategory>
            ))}
          </MobileCategories>
          <MobileContactInfoContainer>
            {renderContactInfoList()}
          </MobileContactInfoContainer>
        </MobilePopupMenu>
      );
    } else {
      const navLink = navLinks[activeMobileIndex];

      if (!navLink) {
        return null;
      }

      const { menu } = navLink;

      return (
        <MobilePopupMenu ref={this.mobilePopupMenuRef} height={height}>
          <MobileBackButton onClick={this.handleMobileBack}>
            <Arrow src={arrowIcon} left />
            <MobileBackButtonTitle>
              {navLinks[activeMobileIndex].title}
            </MobileBackButtonTitle>
          </MobileBackButton>
          {menu.mobile ? menu.mobile(location) : menu.default(location)}
        </MobilePopupMenu>
      );
    }
  };

  render() {
    const { showMobilePopupMenu } = this.state;

    return (
      <Nav>
        <NavInner>
          {this.renderDesktop()}
          {this.renderMobile()}
        </NavInner>
        {this.renderDesktopPopupMenu()}
        {showMobilePopupMenu && this.renderMobilePopupMenu()}
      </Nav>
    );
  }
}

export default withClickOutside(Header);
