import React from "react";

import arrow from "assets/img/icons/orange/arrow-up.svg";

import { Container, TitleWrapper, Title, Arrow, Subtitle } from "./elements";

const MenuLink = ({
  to,
  title,
  subtitle,
  noBorderBelowTitle = false,
  isActive = false,
  noArrow = false,
}) => (
  <Container>
    <TitleWrapper noBorderBelowTitle={noBorderBelowTitle} to={to}>
      <Title isActive={isActive} large={!!subtitle || noArrow}>
        {title}
      </Title>
      {!noArrow && <Arrow src={arrow} />}
    </TitleWrapper>
    {subtitle && <Subtitle>{subtitle}</Subtitle>}
  </Container>
);

export default MenuLink;
