import React from "react";
import Spacer from "../Spacer";
import { MainAxisAlignment, CrossAxisAlignment } from "../AxisAlignment";
import { FlexAlignItem, FlexJustifyContent } from "../FlexBoxAlignment";
import Padding from "../Padding";

interface VStackProps {
  horizontalAlignment?: CrossAxisAlignment;
  verticalAlignment?: MainAxisAlignment;
  padding?: Padding;
  spacing?: React.CSSProperties["flexBasis"];
  style?: React.CSSProperties;
  children: React.ReactNode;
}

export default class VStack extends React.Component<VStackProps> {
  render() {
    const {
      style = {},
      padding = Padding.none(),
      spacing = "",
      children,
      horizontalAlignment = CrossAxisAlignment.CENTER,
      verticalAlignment = MainAxisAlignment.START,
    } = this.props;
    return (
      <div
        className="brex-vstack"
        style={createStyles(
          horizontalAlignment,
          verticalAlignment,
          padding,
          style,
        )}
      >
        {React.Children.map(children, (child, index) => {
          const isNotLastChild = index !== React.Children.count(children) - 1;
          const shouldInsertSpacer = isNotLastChild && spacing;

          if (shouldInsertSpacer) {
            return (
              <>
                {child}
                <Spacer height={spacing} />
              </>
            );
          } else {
            return <>{child}</>;
          }
        })}
      </div>
    );
  }
}

function mapCrossAxisAlignmentToAlignItems(
  crossAxisAlignment: CrossAxisAlignment,
): FlexAlignItem {
  switch (crossAxisAlignment) {
    case CrossAxisAlignment.START:
      return "flex-start";
    case CrossAxisAlignment.CENTER:
      return "center";
    case CrossAxisAlignment.END:
      return "flex-end";
    case CrossAxisAlignment.FILL:
      return "stretch";
    default:
      throw new Error("Invalid xLayout: " + crossAxisAlignment);
  }
}

function mapMainAxisAlignmentToJustifyContent(
  mainAxisAlignment: MainAxisAlignment,
): FlexJustifyContent {
  switch (mainAxisAlignment) {
    case MainAxisAlignment.START:
      return "flex-start";
    case MainAxisAlignment.CENTER:
      return "center";
    case MainAxisAlignment.END:
      return "flex-end";
    case MainAxisAlignment.EVEN:
      return "space-evenly";
    case MainAxisAlignment.SPACE_AROUND:
      return "space-around";
    case MainAxisAlignment.SPACE_BETWEEN:
      return "space-between";
  }
  ensureExhaustiveSwitch(
    mainAxisAlignment,
    "Invalid mainAxisAlignment " + mainAxisAlignment,
  );
}

function ensureExhaustiveSwitch(param: never, message: string): never {
  throw new Error(message);
}

function createStyles(
  xLayout: CrossAxisAlignment,
  yLayout: MainAxisAlignment,
  padding: Padding,
  style: React.CSSProperties,
): React.CSSProperties {
  return {
    height: "100%",
    display: "flex",
    flexDirection: "column",
    paddingTop: padding.paddingTop,
    paddingRight: padding.paddingRight,
    paddingBottom: padding.paddingBottom,
    paddingLeft: padding.paddingLeft,
    justifyContent: mapMainAxisAlignmentToJustifyContent(yLayout),
    alignItems: mapCrossAxisAlignmentToAlignItems(xLayout),
    ...style,
  };
}
