import * as React from "react";
import { Message } from "./elements";
import Toast from "../";
import colors from "styles/colors";
import { InternalProps } from "../types";

const ProductToast = ({ message, hideToast }: InternalProps) => (
  <Toast
    backgroundColor={colors.toastGray}
    height={45}
    onClose={hideToast}
    closeColor={colors.gray1}
  >
    <Message>{message}</Message>
  </Toast>
);

export default ProductToast;
