import React, { useEffect, useContext } from "react";
import ChatBotContext from "contexts/ChatBot";

import { helpshift, drift } from "./helpers.js";
import InlineScriptLoader from "utils/addScript";

const DriftLoader: React.FC<{}> = () => {
  const { initHS } = useContext(ChatBotContext);

  const swapChatBot = (textIdentifiers: Array<String>) => {
    window.drift.on("conversation:buttonClicked", function(data: any) {
      if (textIdentifiers.includes(data.buttonBody)) {
        initHS();
      }
    });
  };

  useEffect(() => {
    if (window.helpshiftConfig) {
      helpshift.hide();
    }
    if (window.drift) {
      drift.show();
    }
  });
  return (
    <InlineScriptLoader
      scriptText={`!function() {var t = window.driftt = window.drift = window.driftt || [];if (!t.init) {if (t.invoked) return void (window.console && console.error && console.error("Drift snippet included twice."));t.invoked = !0, t.methods = [ "identify", "config", "track", "reset", "debug", "show", "ping", "page", "hide", "off", "on" ],t.factory = function(e) {return function() {var n = Array.prototype.slice.call(arguments);return n.unshift(e), t.push(n), t;};}, t.methods.forEach(function(e) {t[e] = t.factory(e);}), t.load = function(t) {var e = 3e5, n = Math.ceil(new Date() / e) * e, o = document.createElement("script");o.type = "text/javascript", o.async = !0, o.crossorigin = "anonymous", o.src = "https://js.driftt.com/include/" + n + "/" + t + ".js";var i = document.getElementsByTagName("script")[0];i.parentNode.insertBefore(o, i);};}}();drift.SNIPPET_VERSION = '0.3.1';window.drift.load('psppdwzxiha9');`}
      windowObject="drift"
      scriptId="drift"
      callback={() => swapChatBot(["Chat with support"])}
    />
  );
};

export default DriftLoader;
