import React from "react";
import Spacer from "../Spacer";
import { MainAxisAlignment, CrossAxisAlignment } from "../AxisAlignment";
import { FlexJustifyContent, FlexAlignItem } from "../FlexBoxAlignment";

interface HStackProps {
  horizontalAlignment?: MainAxisAlignment;
  verticalAlignment?: CrossAxisAlignment;
  padding?: React.CSSProperties["padding"];
  spacing?: React.CSSProperties["flexBasis"];
  style?: React.CSSProperties;
  children: React.ReactNode;
}

export default class HStack extends React.Component<HStackProps> {
  render() {
    const {
      style = {},
      padding = "",
      spacing = "",
      children,
      horizontalAlignment = MainAxisAlignment.START,
      verticalAlignment = CrossAxisAlignment.START,
    } = this.props;
    return (
      <div
        className="brex-hstack"
        style={createStyles(
          horizontalAlignment,
          verticalAlignment,
          padding,
          style,
        )}
      >
        {React.Children.map(children, (child, index) => {
          const isNotLastChild = index !== React.Children.count(children) - 1;
          const shouldInsertSpacer = isNotLastChild && spacing;

          if (shouldInsertSpacer) {
            return (
              <>
                {child}
                <Spacer width={spacing} />
              </>
            );
          } else {
            return <>{child}</>;
          }
        })}
      </div>
    );
  }
}

function mapMainAxisAlignmentToJustifyContent(
  mainAxisAlignment: MainAxisAlignment,
): FlexJustifyContent {
  switch (mainAxisAlignment) {
    case MainAxisAlignment.START:
      return "flex-start";
    case MainAxisAlignment.CENTER:
      return "center";
    case MainAxisAlignment.END:
      return "flex-end";
    case MainAxisAlignment.EVEN:
      return "space-evenly";
    case MainAxisAlignment.SPACE_AROUND:
      return "space-around";
    case MainAxisAlignment.SPACE_BETWEEN:
      return "space-between";
    default:
      throw new Error("Invalid xLayout: " + mainAxisAlignment);
  }
}

function mapCrossAxisAlignmentToAlignItems(
  crossAxisAlignment: CrossAxisAlignment,
): FlexAlignItem {
  switch (crossAxisAlignment) {
    case CrossAxisAlignment.START:
      return "flex-start";
    case CrossAxisAlignment.CENTER:
      return "center";
    case CrossAxisAlignment.END:
      return "flex-end";
    case CrossAxisAlignment.FILL:
      return "stretch";
    default:
      throw new Error("Invalid yLayout: " + crossAxisAlignment);
  }
}

function createStyles(
  horizontalAlignment: MainAxisAlignment,
  verticalAlignment: CrossAxisAlignment,
  padding: React.CSSProperties["padding"],
  style: React.CSSProperties,
): React.CSSProperties {
  return {
    flexDirection: "row",
    width: "100%",
    display: "flex",
    padding,
    justifyContent: mapMainAxisAlignmentToJustifyContent(horizontalAlignment),
    alignItems: mapCrossAxisAlignmentToAlignItems(verticalAlignment),
    ...style,
  };
}
